// A map of breakpoints.
$breakpoints: (
  xs: 481px,
  sm: 768px,
  md: 980px,
  lg: 1400px,
);

//
//  RESPOND ABOVE
//––––––––––––––––––––––––––––––––––––––––––––––––––

// @include respond-above(sm) {}
@mixin respond-above($breakpoint) {
  // If the breakpoint exists in the map.
  @if map-has-key($breakpoints, $breakpoint) {
    // Get the breakpoint value.
    $breakpoint-value: map-get($breakpoints, $breakpoint);

    // Write the media query. 
    @media (min-width: $breakpoint-value) {
      @content;
    }

    // If the breakpoint doesn't exist in the map.
  } @else {
    // Log a warning.
    @warn 'Invalid breakpoint: #{$breakpoint}.';
  }
}

@mixin thisco-messages(){
  .thisco-messages {   
    padding: 1rem;
    display: grid;
    @include respond-above(sm){
      padding: 2px;
      font-size: .7em;
    }
    .thisco-message > div {
      padding: 5px;
      border-radius: 5px;
      background: crimson;
      color: white;
      // font-weight: bold;
      margin-bottom: 5px;
      box-shadow: 1px 1px 5px rgb(0 0 0 / 0.3);
      animation: appear .3s, flash .1s 1s;
    }
    .thisco-message.message-dead {
      animation: disappear .3s;
    }
  }
}

$obs-width: 100px;

#thiscoObs {
  position: absolute;
  top: 0px;
  width: 100vw;
  padding-top: 5rem;
  left: 0px;
  pointer-events: none;
  > .thisco-obs-content {
    position: fixed;
    @include thisco-messages();
    .thisco-progress-dial {
      display: none;
    }
    .thisco-progress-bar {
      opacity: 0.7;
      display: inherit;
      // background-image:
      //     radial-gradient(circle at center, var(--thisco-pink) 66%, transparent 66%),
      //     linear-gradient(var(--thisco-pink), var(--thisco-pink)),
      //     radial-gradient(circle at center, var(--thisco-pink) 66%, transparent 66%);
      // background-size: 2px 2px, calc(90% - 4px) 2px, 2px 2px;
      // background-repeat: no-repeat;
      // background-position: calc(10% + 2px) center, center, calc(90% - 2px) center;
      width: 100%;
      height: 1rem;
      > .progress {
        height: 8px;
        background-image: radial-gradient(
            circle at center,
            var(--thisco-red) 66%,
            transparent 66%
          ),
          linear-gradient(var(--thisco-red), var(--thisco-red));
        background-position: right, left;
        background-size: 8px 8px, calc(100% - 4px);
        background-repeat: no-repeat;
        transition: width 0.2s cubic-bezier(0.34, 1.56, 0.64, 1);
      }
    }
  }

  @include respond-above(sm) {
    position: fixed;
    top: 0px;
    width: $obs-width;
    height: 1000px;
    padding-top: calc(8.5rem + 2em);
    > .thisco-obs-content {
      min-height: 13rem;
      display: block;
      position: relative;
      &:before {
        opacity: 0.2;
        position: absolute;
        display: inline-block;
        background-image: radial-gradient(
            circle at center,
            var(--thisco-pink) 64%,
            transparent 66%
          ),
          linear-gradient(var(--thisco-pink), var(--thisco-pink)),
          radial-gradient(
            circle at center,
            var(--thisco-pink) 64%,
            transparent 66%
          );
        content: " ";
        height: calc(100% + 1em);
        width: 5px;
        left: -10px;
        top: -0.5em;
        background-repeat: no-repeat;
        background-position: center top, center, center bottom;
        background-size: 5px 5px, 100% calc(100% - 5px), 5px 5px;
      }
      .thisco-progress-dial {
        display: inherit;
      }
      .thisco-progress-bar {
        display: none;
      }
    }
  }
}

table.ProgressBarContainer {
  font-size: 0px;
  color: transparent;
}

@keyframes appear {
  from {
    opacity: 0;
    scale: 1.2;
  }
  to {
    opacity: 1;
    scale : 1;
  }
}

@keyframes disappear {
  to {
    opacity: 0;
    // translate: 0px -10px;
    scale : .8;
  }
}